import { useState, useEffect } from "react";
import styled from "styled-components";
import { Hero, TextBlock } from "components";
import { dataRus, dataEng } from "data";
import { IconMoon, IconSun } from "assets/icons";
import { GlobalStyles } from "styles";

export const App = () => {
  const [language, setLanguage] = useState(false);
  const [themes, setTheme] = useState("light");
  const [data, setData] = useState([]);

  useEffect(() => {
    language ? setData(dataRus) : setData(dataEng);
  }, [language]);

  const dark = () => {
    document.documentElement.style.setProperty("--dark", "white");
    document.documentElement.style.setProperty("--light", "#000");
  };

  const light = () => {
    document.documentElement.style.setProperty("--dark", "#000");
    document.documentElement.style.setProperty("--light", "white");
  };

  useEffect(() => {
    function setThemePreference() {
      let d = new Date();
      let currentHour = d.getHours();
      if (currentHour >= 19 || currentHour <= 6) {
        dark();
        setTheme("dark");
      } else {
        light();
        setTheme("light");
      }
    }
    setThemePreference();
  }, []);

  const changeTheme = () => {
    if (themes === "light") {
      setTheme("dark");
      dark();
    } else {
      setTheme("light");
      light();
    }
  };

  return (
    <>
      <GlobalStyles />
      <Wrapper>
        <div className="one">
          <Hero engBollean={language} />
        </div>
        <div className="two">
          {data.map((item, index) => (
            <TextBlock key={index} textData={item} />
          ))}
        </div>
        <div onClick={changeTheme} className="change-theme-toggle">
          {themes === "light" ? <IconSun /> : <IconMoon />}
          {/* {themes === "dark" && <IconMoon />} */}
        </div>
        <div
          onClick={() => {
            setLanguage(!language);
          }}
          className="change-language-toggle"
        >
          <div style={{ userSelect: "none" }}>{language ? "RU" : "EN"}</div>
          <div className="triangle"></div>
        </div>
      </Wrapper>
    </>
  );
};

const Wrapper = styled.div`
  display: flex;
  justify-content: flex-end;
  @media (max-width: 555px) {
    justify-content: flex-start;
    flex-direction: column;
  }
`;
